@import url('https://fonts.googleapis.com/css2?family=Bubblegum+Sans&display=swap');
@import '../../../scss/index.scss';

.main-screen {
    font-family: 'Bubblegum Sans', cursive;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    background-color: $background-common;

    .title {
        display: flex;
        justify-content: space-evenly;
        height: 70px;
        width: 150px;
        h2 {
            color: #fff;
        }
    }

    h1 {
        font-size: 8em;
        color: #fff;
    }

    .nav {
        width: 750px;
        height: 120px;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        a {
            font-size: 2em;
            text-decoration: none;
            color: #fff;
            transition: all .2s;

            &:hover {
                font-size: 2.2em;
                border-bottom: 2px solid #eeeeee;
            }
        }
    }

    .about-pipnd {
        width: 100vw;
        height: 100vh;
        margin: auto;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 2rem;
        transform: scale(0);
        transform-origin: right;
        transition: all .5s;
        z-index: 1;

        &__content {
            width: 80vw;
            height: 80vh;
            background-color: #1184f7;
            padding: 50px;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            font-family: 'Roboto', sans-serif;

            .btn-close {
                position: absolute;
                right: 5%;
                top: 5%;
                width: 80px;
                height: 35px;
                background-color: #fff;
                border: none;
                font-size: 16px;
                cursor: pointer;
                transition: all .2s;

                &:hover {
                    background-color: $background-common;
                    color: #fff;
                }
            }

            .text {
                width: 600px;
                h1 {
                    font-size: 2em;
                    font-weight: 700;
                }

                p {
                    color: #fff;
                    font-size: 18px;
                    font-weight: 400;

                    a {
                        color: #fff;
                        font-weight: bold;

                        &:hover {
                            border-bottom: 1px solid #fff;
                        }
                    }
                }
            }

            .inclusion-img {
                width: 500px;
                height: 500px;
                object-fit: cover;
                border-radius: 50%;
            }
        }

    }
}

.about-pipnd-change {
    transform: scale(1) !important;
    border-radius: 0 !important;
    padding: 0 !important;
}