@import '../../../scss/index.scss';

.recursos-wp {
    width: 100vw;
    height: 100vh;
    background-color: $background-common;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    h1 {
        font-size: 3em;
    }

    p {
        font-size: 18px;
    }

    .tutorial {
        width: 95%;
        height: 800px;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        
        img {
            object-fit: contain;
        }

        .tut {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-evenly;
        }
    }
}