@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  user-select: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.goback {
  position: absolute;
  top: 5%;
  left: 3%;
  width: 120px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;

  a {
      color: #fff;
      margin-left: 10px;
      font-size: 18px;
      font-weight: bold;
  }

  i {
      color: #fff;
  }
}
