@import '../../../../scss/index.scss';

.actividades-main {
    height: 100vh;
    width: 100vw;
    background-color: $background-common;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
    overflow: hidden !important;

    .card {
        height: 300px;
        width: 300px;
        background-color: #fff;
        border-radius: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: opacity .2s;

        &:hover {
            h2 {
                opacity: 0;
            }
        }

        &.pictogramas {
            &:hover {
                background-image: url(../../../../assets/picto.jpg);
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
            }
        }

        &.memotests {
            &:hover {
                background-image: url(../../../../assets/memo.png);
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
            }
        }

        h2 {
            font-size: 28px;
        }
    }
}